.square {
  min-width: 24px;
  height: 24px;
  margin: 1px;
  padding: 2px;
  border-width: 1px;
}

.ball {
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
  border-bottom-left-radius: 50%;
  min-width: 24px;
  height: 24px;
  margin: 1px;
  padding: 2px;
  border-width: 1px;
  /* box-shadow: 0 14px 0px -10px black; */
}

button.ball {
  margin: 0px;
}

.span {
  display: inline-block;
  font-size: 16px;
  line-height: 24px;
  border-style: solid;
  border-color: gray;
  font-weight: normal;
  text-align: center;
}